import React, { useRef, useEffect } from 'react';
import './Background.scss';

const Background = () => {
  const backgroundRef = useRef<HTMLHeadingElement>(null);
  const getRandomIntWithMin = (min: number, max: number) => Math.floor(Math.random() * (max - min) + min);
  const getRandomInt = (max: number) => Math.floor(Math.random() * max);
  const percentage = (partialValue: number, totalValue: number) => (100 * partialValue) / totalValue;

  // Positioning stars
  useEffect(() => {
    Array.from(document.getElementsByClassName('star')).forEach((element: any) => {
      element.style.opacity = 1;
      setTimeout(() => {
        element.style.top = `${getRandomInt(window.innerHeight)}px`;
        element.style.left = `${getRandomInt(window.innerWidth)}px`;
      }, 100);
    });
  }, []);

  // Mouse move effect
  document.addEventListener('mousemove', (e: any) => {
    if (backgroundRef.current) {
      const xMargin = percentage(e.clientX, window.innerWidth) / 10;
      const yMargin = percentage(e.clientY, window.innerHeight) / 10;
      backgroundRef.current.style.marginLeft = `${xMargin}px`;
      backgroundRef.current.style.marginTop = `${yMargin}px`;
    }
  });

  const stars = () => {
    return [...Array(getRandomIntWithMin(200, 300)).keys()].map((i: number) => {
      const size = getRandomIntWithMin(2, 8);
      return (
        <div
          key={i}
          className="star"
          style={{
            height: `${size}px`,
            width: `${size}px`,
          }}
        />
      );
    });
  }

  return (
    <div className="background" ref={backgroundRef}>
      {stars()}
    </div>
  );
};

export default Background;
